* {
  margin: 0;
  padding: 0;
}

body {
  background-image: url("./assets/pattern.jpg");
  background-position: center center;
  background-size: cover;
}

.bg-wrap {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 220px;
  width: 350px;
  background-color: rgb(159 128 84);
  border-radius: 10px;
  padding: 50px;
}
input[type="text"] {
  padding: 10px;
  height: 35px;
  border-radius: 10px;
  margin-top: 10px;
  width: 95%;
  border: 0;
}

.btn-submit {
  width: 100%;
  color: white;
  outline: none;
  border: 0;
  background-color: thistle;
  padding: 15px;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
}

/* Game screen */
.game-screen {
  height: calc(100vh - 100px);
  display: flex;
  justify-content: space-around;
  padding: 25px;
  flex-direction: column;
  width: 70%;
  float: left;
}

.card-holder {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.flipped-card,
.drawn-card,
.defuse-card,
.no-card {
  display: flex;
  justify-content: center;
  align-items: end;
  height: 355px;
  width: 245px;
  padding: 10px;
  cursor: pointer;
  box-shadow: 5px 5px 15px 3px rgb(0 0 0 / 20%);
  border: 5px solid white;
  position: relative;
  border-radius: 20px;
}

/* .flipped-card:hover {
  transform: rotateY(360deg);
} */
.flipped-card-wrapper {
  position: relative;
  width: 255px;
}
.flipped-card {
  background-image: url("./assets/cardbg.jpg");
  background-size: 100% 100%;
  border-radius: 20px;
  position: absolute;
  transform-origin: bottom center;
  transition: transform 700ms;
}

li.flipped-card:nth-child(1) {
  transform: rotate(-6deg);
}
li.flipped-card:nth-child(2) {
  transform: rotate(-3deg);
}
li.flipped-card:nth-child(3) {
  transform: rotate(0deg);
}
li.flipped-card:nth-child(4) {
  transform: rotate(3deg);
}
li.flipped-card:nth-child(5) {
  transform: rotate(6deg);
}

.drawn-card {
  background-image: url("./assets/drawn.jpg");
}

.defuse-card {
  background-image: url("./assets/defuseBomb.jpg");
}

.no-card {
  background-image: url("./assets/no-card.jpg");
}

.card-labels {
  position: absolute;
  bottom: 0;
  background-color: rgb(177, 101, 35);
  padding: 10px;
  font-weight: 700;
  color: white;
  width: 100%;
  border-radius: 10px;
  font-size: 16px;
  border: 1px solid orange;
}
.btn-holder {
  display: flex;
  justify-content: center;
  align-items: center;
}
.control-btn {
  font-size: 18px;
  padding: 15px 60px;
  margin: 10px;
  border: none;
  outline: none;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  transition: all 200ms;
}

.control-btn:hover {
  transform: translateY(-8px);
  box-shadow: 0 5px 10px 1px rgba(0, 0, 0, 0.5);
}

.control-btn:active {
  transform: translateY(-5px);
  box-shadow: 0 5px 5px 1px rgba(0, 0, 0, 0.5);
}

.control-btn.red {
  background-color: #d10000;
}

/* Leaderboard goes here */
.leader-panel {
  padding: 20px;
  display: flex;
  border-left: 1px dotted black;
  min-height: calc(100vh - 42px);
  flex-direction: column;
  flex-wrap: wrap;
  background-color: rgba(218, 190, 116, 0.7);
}

td {
  padding: 15px;
  font-size: 20px;
}

tr {
  background-color: beige;
}

.game-status {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
  align-self: center;
  width: 80%;
  font-size: 20px;
  background-color: rgba(218, 190, 116, 0.7);
  border-radius: 8px;
}

@media (max-width: 600px) {
  .game-screen {
    width: 100%;
    padding: 0;
    height: 430px;
  }
  .card-holder {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .flipped-card,
  .drawn-card,
  .defuse-card,
  .no-card {
    height: 150px;
    width: 100px;
    padding: 0px;
    border: 2px solid white;
    background-size: 100%;
  }
  .flipped-card-wrapper {
    width: 112px;
    height: 150px;
  }
  .card-labels {
    font-size: 12px;
    padding: 5px;
  }
  .leader-panel {
    min-height: 100%;
  }
}
